import React from 'react';

const Dashboard = React.lazy(() => import('../pages/Dashboard'));
const Users = React.lazy(() => import('../pages/Users'));
const Sliders = React.lazy(() => import('../pages/Sliders'));
const Categorias = React.lazy(() => import('../pages/Categorias'));
const Categoria = React.lazy(() => import('../pages/Categoria'));
const Idades = React.lazy(() => import('../pages/Idades'));
const Unidades = React.lazy(() => import('../pages/Unidades'));
const Pelagems = React.lazy(() => import('../pages/Pelagems'));
const Andrologias = React.lazy(() => import('../pages/Andrologias'));
const Pesos = React.lazy(() => import('../pages/Pesos'));
const Litros = React.lazy(() => import('../pages/Litros'));
const Registros = React.lazy(() => import('../pages/Registros'));
const Depoimentos = React.lazy(() => import('../pages/Depoimentos'));

const routes = [
  {
    path: 'admin/',
    name: 'Dashboard',
    component: Dashboard,
  },
  {
    path: 'admin/:token',
    name: 'Dashboard',
    component: Dashboard,
  },
  {
    path: 'admin/usuarios',
    name: 'Usuários',
    component: Users,
  },
  {
    path: 'admin/sliders',
    name: 'Sliders',
    component: Sliders,
  },
  {
    path: 'admin/categorias',
    name: 'Categorias',
    component: Categorias,
  },
  {
    path: 'admin/categorias/categoria',
    name: 'Categoria',
    component: Categoria,
  },
  {
    path: 'admin/categorias/categoria/:id',
    name: 'Categoria',
    component: Categoria,
  },
  {
    path: 'admin/idades',
    name: 'Idades',
    component: Idades,
  },
  {
    path: 'admin/unidades',
    name: 'Unidades',
    component: Unidades,
  },
  {
    path: 'admin/pelagems',
    name: 'Pelagens',
    component: Pelagems,
  },
  {
    path: 'admin/andrologias',
    name: 'Andrologias',
    component: Andrologias,
  },
  {
    path: 'admin/pesos',
    name: 'Pesos',
    component: Pesos,
  },
  {
    path: 'admin/litros',
    name: 'Litros',
    component: Litros,
  },
  {
    path: 'admin/registros',
    name: 'Registros',
    component: Registros,
  },
  {
    path: 'admin/depoimentos',
    name: 'Depoimentos',
    component: Depoimentos,
  },
];

export default routes;
